'use strict';

import format from '@stdlib/error-tools-fmtprodmsg';

// MODULES //

import strides2offset from '@stdlib/ndarray-base-strides2offset';


// MAIN //

/**
* Transpose a matrix (or a stack of matrices).
*
* @param {ndarray} x - input array
* @throws {Error} must provide an array with two or more dimensions
* @returns {ndarray} ndarray
*
* @example
* import array from '@stdlib/ndarray-array';
*
* var x = array( [ [ 1, 2, 3 ], [ 4, 5, 6 ] ] );
* // returns <ndarray>
*
* var sh = x.shape;
* // returns [ 2, 3 ]
*
* var y = transpose( x );
* // returns <ndarray>
*
* sh = y.shape;
* // returns [ 3, 2 ]
*
* var bool = ( x.data === y.data );
* // returns true
*
* bool = ( x.get( 0, 1 ) === y.get( 1, 0 ) );
* // returns true
*/
function transpose( x ) {
	var tmp;
	var sh;
	var st;
	var N;

	sh = x.shape;   // WARNING: we assume that `sh` is a copy, which is true of `ndarray` instances!
	N = sh.length;
	if ( N < 2 ) {
		throw new Error( format( '0Mi0c' ) );
	}
	st = x.strides; // WARNING: we assume that `st` is a copy, which is true of `ndarray` instances!

	tmp = sh[ N-2 ];
	sh[ N-2 ] = sh[ N-1 ];
	sh[ N-1 ] = tmp;

	tmp = st[ N-2 ];
	st[ N-2 ] = st[ N-1 ];
	st[ N-1 ] = tmp;

	return new x.constructor( x.dtype, x.data, sh, st, strides2offset( sh, st ), x.order ); // eslint-disable-line max-len
}


// EXPORTS //

export default transpose;
